module.exports = "<p><strong>sysctl</strong><code>/proc/sys</code>TCP/<a href=\"https://philipding.github.io/linux-command/ip\" title=\"ip\">ip</a> sysctl</p>\n<h3 id=\"-\"></h3>\n<pre><code>sysctl()()\n</code></pre><h3 id=\"-\"></h3>\n<pre><code>-n\n-e\n-N\n-[w](https://philipding.github.io/linux-command/w &quot;w&quot;)sysctl\n-p/etc/sysctl.conf\n-a\n-A\n</code></pre><h3 id=\"-\"></h3>\n<p>=</p>\n<h3 id=\"-\"></h3>\n<p></p>\n<p>sysctl -a</p>\n<p><code>kern.maxproc</code></p>\n<p>sysctl kern.maxproc kern.maxproc: 1044</p>\n<p><code>variable=value</code></p>\n<pre><code>sysctl kern.maxfiles=5000\nkern.maxfiles: 2088 -&gt; 5000\n</code></pre><p>sysctlsysctl.confsysctl.confrc.conf<code>variable=value</code></p>\n<p>sysctl 1 &#39;<a href=\"https://philipding.github.io/linux-command/yes\" title=\"yes\">yes</a>&#39; 0 &#39;no&#39;</p>\n<pre><code>sysctl -w kernel.sysrq=0\nsysctl -w kernel.core_uses_pid=1\nsysctl -w net.ipv4.conf.default.accept_redirects=0\nsysctl -w net.ipv4.conf.default.accept_source_route=0\nsysctl -w net.ipv4.conf.default.rp_filter=1\nsysctl -w net.ipv4.tcp_syncookies=1\nsysctl -w net.ipv4.tcp_max_syn_backlog=2048\nsysctl -w net.ipv4.tcp_fin_timeout=30\nsysctl -w net.ipv4.tcp_synack_retries=2\nsysctl -w net.ipv4.tcp_keepalive_time=3600\nsysctl -w net.ipv4.tcp_window_scaling=1\nsysctl -w net.ipv4.tcp_sack=1\n</code></pre><h3 id=\"-sysctl\">sysctl</h3>\n<p><code>/etc/sysctl.conf</code></p>\n<p></p>\n<pre><code># Controls source [route](https://philipding.github.io/linux-command/route &quot;route&quot;) verification\n# Default should work for all interfaces\nnet.ipv4.conf.default.rp_filter = 1\n# net.ipv4.conf.all.rp_filter = 1\n# net.ipv4.conf.lo.rp_filter = 1\n# net.ipv4.conf.eth0.rp_filter = 1\n\n# Disables IP source routing\n# Default should work for all interfaces\nnet.ipv4.conf.default.accept_source_route = 0\n# net.ipv4.conf.all.accept_source_route = 0\n# net.ipv4.conf.lo.accept_source_route = 0\n# net.ipv4.conf.eth0.accept_source_route = 0\n\n# Controls the System Request debugging functionality of the kernel\nkernel.sysrq = 0\n\n# Controls whether core dumps will append the PID to the core filename.\n# Useful for debugging multi-threaded applications.\nkernel.core_uses_pid = 1\n\n# Increase maximum amount of memory allocated to shm\n# Only uncomment if needed!\n# kernel.shmmax = 67108864\n\n# Disable ICMP Redirect Acceptance\n# Default should work for all interfaces\nnet.ipv4.conf.default.accept_redirects = 0\n# net.ipv4.conf.all.accept_redirects = 0\n# net.ipv4.conf.lo.accept_redirects = 0\n# net.ipv4.conf.eth0.accept_redirects = 0\n\n# [enable](https://philipding.github.io/linux-command/enable &quot;enable&quot;) Log Spoofed Packets, Source Routed Packets, Redirect Packets\n# Default should work for all interfaces\nnet.ipv4.conf.default.log_martians = 1\n# net.ipv4.conf.all.log_martians = 1\n# net.ipv4.conf.lo.log_martians = 1\n# net.ipv4.conf.eth0.log_martians = 1\n\n# Decrease the [time](https://philipding.github.io/linux-command/time &quot;time&quot;) default value for tcp_fin_timeout connection\nnet.ipv4.tcp_fin_timeout = 25\n\n# Decrease the time default value for tcp_keepalive_time connection\nnet.ipv4.tcp_keepalive_time = 1200\n\n# Turn on the tcp_window_scaling\nnet.ipv4.tcp_window_scaling = 1\n\n# Turn on the tcp_sack\nnet.ipv4.tcp_sack = 1\n\n# tcp_fack should be on because of sack\nnet.ipv4.tcp_fack = 1\n\n# Turn on the tcp_timestamps\nnet.ipv4.tcp_timestamps = 1\n\n# Enable TCP SYN Cookie Protection\nnet.ipv4.tcp_syncookies = 1\n\n# Enable ignoring broadcasts request\nnet.ipv4.icmp_echo_ignore_broadcasts = 1\n\n# Enable bad error message Protection\nnet.ipv4.icmp_ignore_bogus_error_responses = 1\n\n# [make](https://philipding.github.io/linux-command/make &quot;make&quot;) [more](https://philipding.github.io/linux-command/more &quot;more&quot;) local ports available\n# net.ipv4.ip_local_port_range = 1024 65000\n\n# [set](https://philipding.github.io/linux-command/set &quot;set&quot;) TCP Re-Ordering value in kernel to 5\nnet.ipv4.tcp_reordering = 5\n\n# Lower syn retry rates\nnet.ipv4.tcp_synack_retries = 2\nnet.ipv4.tcp_syn_retries = 3\n\n# Set Max SYN Backlog to 2048\nnet.ipv4.tcp_max_syn_backlog = 2048\n\n# Various Settings\nnet.core.netdev_max_backlog = 1024\n\n# Increase the maximum number of skb-heads to be cached\nnet.core.hot_list_length = 256\n\n# Increase the tcp-time-[wait](https://philipding.github.io/linux-command/wait &quot;wait&quot;) buckets pool size\nnet.ipv4.tcp_max_tw_buckets = 360000\n\n# This will increase the amount of memory available for socket input/output queues\nnet.core.rmem_default = 65535\nnet.core.rmem_max = 8388608\nnet.ipv4.tcp_rmem = 4096 87380 8388608\nnet.core.wmem_default = 65535\nnet.core.wmem_max = 8388608\nnet.ipv4.tcp_wmem = 4096 65535 8388608\nnet.ipv4.tcp_mem = 8388608 8388608 8388608\nnet.core.optmem_max = 40960\n</code></pre><p> <a href=\"https://philipding.github.io/linux-command/ping\" title=\"ping\">ping</a> </p>\n<pre><code># Disable ping requests\nnet.ipv4.icmp_echo_ignore_all = 1\n</code></pre><p></p>\n<pre><code>/sbin/sysctl -p\n/sbin/sysctl -w net.ipv4.route.flush=1\n</code></pre>";

